
import { Swipe, SwipeItem } from 'vue-swipe'
import Masker from 'vux/src/components/masker/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'

import axios from 'axios'
var noticeIcon = require('../assets/notice.png')
var applyIcon = require('../assets/apply.png')
var unionIcon = require('../assets/union.png')
var newsIcon = require('../assets/news.png')
export default {
  components: {
    Swipe,
    SwipeItem,
    Masker,
    Marquee,
    MarqueeItem
  },
  data () {
    return {
      isEmpty: false,
      items: [],
      currentPage: 1,
      noticeIcon: noticeIcon,
      moduleIconList: [{
        title: '我要申请',
        img: applyIcon,
        link: '/applyList'
      }, {
        title: '我要加盟',
        img: unionIcon,
        link: '/Join'
      }, {
        title: '资讯',
        img: newsIcon,
        link: '/newsList'
      }],
      list: []
    }
  },
  methods: {
    getBanners () {
      var obj = this
      axios.post(this.getHost + '/home/banners')
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            obj.items = data.data
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    getNews (currentPage) {
      var obj = this
      axios.post(this.getHost + '/home/newsList', {currentPage: currentPage})
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            if (data.data.length === 0) {
              obj.isEmpty = true
              console.log('a' + obj.isEmpty)
              return
            }
            obj.list.push.apply(obj.list, data.data)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    handleScroll () {
      var scrolled = window.scrollY > 0
      if (scrolled && this.isEmpty) {
        console.log('b' + this.isEmpty)
        this.getNews(this.currentPage ++)
      }
    }
  },
  mounted: function () {
    this.getBanners()
    this.getNews(this.currentPage)
    window.addEventListener('scroll', this.handleScroll)
  }
}



