
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
  mounted () {
    setTimeout(() => {
      this.money = -1024
    }, 2000)
  },
  components: {
    Group,
    Cell,
    Icon,
    Divider
  },
  methods: {
    onClick () {
      console.log('on click')
    }
  },
  data () {
    return {
      list: [{
        title: 'POS 机',
        icon: 'success',
        link: 'pos'
      }, {
        title: '信用卡',
        icon: 'success-circle',
        link: 'creditCard'
      }, {
        title: '贷款',
        icon: 'waiting',
        link: 'loan'
      }, {
        title: '车险',
        icon: 'waiting-circle',
        link: 'insurance'
      }]
    }
  }
}



