
import Blur from 'vux/src/components/blur/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import axios from 'axios'
export default {
  components: {
    Blur,
    Card,
    Group,
    Cell
  },
  data () {
    return {
      url: 'https://o3e85j0cv.qnssl.com/waterway-107810__340.jpg',
      numbers: {},
      info: { nickname: '', mobile: '', address: '', avatar: '' }
    }
  },
  methods: {
    myApply (type) {
      location.href = '#/myApply/' + type
    },
    getStat () {
      var obj = this
      axios.post(this.getHost + '/info/applyStat')
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            if (data.data) {
              obj.numbers = data.data
            }
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    getInfo () {
      var obj = this
      axios.post(this.getHost + '/info/getInfo')
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            if (data) {
              data.data.address = data.data.province + data.data.city
              obj.info = data.data
            }
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  },
  mounted: function () {
    this.getStat()
    this.getInfo()
  }
}



