
import axios from 'axios'
import Alert from 'vux/src/components/alert/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

export default {
  components: {
    Alert,
    Group,
    Selector,
    XInput,
    XButton,
    Checklist,
    Cell,
    Radio
  },
  data () {
    return {
      show: false,
      errorInfo: '操作失败',
      name: '',
      mobile: '',
      sum: '',
      workList: [ '公司上班', '个人经营企业主' ],
      workValue: '公司上班',
      assetList: [ '房', '车', '营业执照', '社保', '保单' ],
      chooseAsset: []
    }
  },
  methods: {
    chooseWorkValue (val) {
      this.workValue = val
    },
    submitApply () {
      if (!this.name) {
        this.errorInfo = '姓名不能为空'
        this.show = true
        return
      }
      if (!this.mobile) {
        this.errorInfo = '手机号码不能为空'
        this.show = true
        return
      }
      var obj = this
      axios.post(this.getHost + '/info/submitApply', { type: 3, apply_type: this.workValue, name: this.name, mobile: this.mobile, sum: this.sum, assets: this.chooseAsset })
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            location.href = '#/msg'
          } else {
            obj.errorInfo = data.errorInfo
            obj.show = true
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}



