
import axios from 'axios'
import Masker from 'vux/src/components/masker/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'

export default {
  components: {
    Masker,
    Marquee,
    MarqueeItem
  },
  data () {
    return {
      isEmpty: false,
      currentPage: 1,
      list: []
    }
  },
  methods: {
    getNews (currentPage) {
      var obj = this
      axios.post(this.getHost + '/home/newsList', {currentPage: currentPage})
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            if (data.data.length === 0) {
              obj.isEmpty = true
              console.log('a' + obj.isEmpty)
              return
            }
            obj.list.push.apply(obj.list, data.data)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    handleScroll () {
      var scrolled = window.scrollY > 0
      if (scrolled && this.isEmpty) {
        console.log('b' + this.isEmpty)
        this.getNews(this.currentPage ++)
      }
    }
  },
  mounted: function () {
    this.getNews(this.currentPage)
    window.addEventListener('scroll', this.handleScroll)
  }
}



