
import axios from 'axios'
import Alert from 'vux/src/components/alert/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
  components: {
    Alert,
    Group,
    Selector,
    XInput,
    XButton,
    Checklist,
    Cell
  },
  data () {
    return {
      show: false,
      errorInfo: '操作失败',
      name: '',
      mobile: '',
      quota: '',
      showType: false,
      assetList: [ '房', '车', '营业执照', '社保' ],
      chooseAsset: [],
      bankList: [ '中信银行', '广发银行', '招商银行', '汇丰银行', '广州银行', '农商银行', '交通银行', '邮政银行' ],
      chooseBank: [],
      defaultValue: '无',
      plainList: ['有', '无']
    }
  },
  methods: {
    onChange (val) {
      if (val === '有') {
        this.showType = true
      } else {
        this.showType = false
      }
    },
    submitApply () {
      if (!this.name) {
        this.errorInfo = '姓名不能为空'
        this.show = true
        return
      }
      if (!this.mobile) {
        this.errorInfo = '手机号码不能为空'
        this.show = true
        return
      }
      if (!this.bankList) {
        this.errorInfo = '银行不能为空'
        this.show = true
        return
      }
      var obj = this
      axios.post(this.getHost + '/info/submitApply', { type: 2, apply_type: this.bankList[0] + '...', name: this.name, mobile: this.mobile, credit_card: this.defaultValue, quota: this.quota, assets: this.chooseAsset, banks: this.chooseBank })
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            location.href = '#/msg'
          } else {
            obj.errorInfo = data.errorInfo
            obj.show = true
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}



